import { RefObject, useEffect } from 'react'

export const useOnClickOutside = (ref: RefObject<HTMLElement>, close: () => void) => {
  useEffect(() => {
    let counter = -1

    const closeSelect = (e: MouseEvent) => {
      console.log('clickOutside')
      counter += 1
      if (
        counter &&
        ref.current &&
        !ref.current.contains(e.target as Node) &&
        !e.composedPath().includes(ref.current)
      ) {
        close()
      }
    }

    document.body.addEventListener('click', closeSelect)

    return () => document.body.removeEventListener('click', closeSelect)
  }, [])
}
